
.layout-page {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  font-size: 1.4rem;
  color: #333;
}
.layout-page .layout-header,
.layout-page .layout-content,
.layout-page .layout-footer {
  position: absolute;
  left: 0;
  width: 100%;
}
.layout-page .layout-header {
  top: 0;
}
.layout-page .layout-content {
  overflow: hidden;
  overflow-y: auto;
}
.layout-page .layout-footer {
  bottom: 0;
}


