
.publish-page .layout-content {
  padding-top: 16px;
}
.publish-page .item {
  padding: 0 12px;
}
.publish-page .item .title {
  padding-bottom: 10px;
  font-size: 1.5rem;
}
.publish-page .item input,
.publish-page .item select,
.publish-page .item textarea {
  border-radius: 5px;
  background-color: #fff;
  width: 100%;
  height: 42px;
  line-height: 42px;
  padding: 0 12px;
  margin-bottom: 15px;
  outline: none;
  border: 0;
  font-size: 1.4rem;
}
.publish-page .item textarea {
  width: 100%;
  padding: 12px;
  height: 134px;
  line-height: 2rem;
  font-size: 1.4rem;
  resize: none;
  outline: none;
}
.publish-page .item button {
  border: 0;
  border-radius: 4px;
  text-align: center;
  width: 100%;
  margin: 30px auto;
  padding: 10px 0;
  background-color: #2594e9;
  color: #fff;
  font-size: 1.8rem;
  font-weight: 200;
}


