
.login-page {
  padding: 100px 12px 12px 15px;
  background-color: #fff;
  bottom: 0;
  transform: translateZ(0);
  z-index: 50;
}
.login-page.login-enter,
.login-page.login-leave-to {
  top: 1000px;
}
.login-page.login-enter-active,
.login-page.login-leave-active {
  transition: top 0.3s cubic-bezier(0.46, 0.71, 0.69, 0.55);
}
.login-page .header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #2594e9;
  height: 44px;
  line-height: 44px;
  color: #fff;
  text-align: center;
}
.login-page .header .btn-back {
  position: absolute;
  width: 30px;
  height: 44px;
  top: 0;
  left: 0;
}
.login-page .header .btn-back:before {
  content: '';
  position: absolute;
  top: 17px;
  left: 12px;
  width: 10px;
  height: 10px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: rotate(45deg);
}
.login-page input {
  width: 100%;
  border: 0;
  border-bottom: 1px solid #ebebeb;
  height: 42px;
  line-height: 42px;
  padding: 0 5px;
  font-size: 1.4rem;
}
.login-page button {
  border: 0;
  border-radius: 4px;
  text-align: center;
  width: 100%;
  margin: 30px auto;
  padding: 10px 0;
  background-color: #2594e9;
  color: #fff;
  font-size: 1.6rem;
  font-weight: 200;
}


