
.loading-backdrop {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: rgba(255,255,255,0.8);
}
.loading-backdrop .load-container {
  border: 1px solid rgba(255,255,255,0.2);
  width: 140px;
  height: 140px;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  margin-top: 250px;
  border-radius: 5px;
}
.loading-backdrop .loading .loader:before,
.loading-backdrop .loading .loader:after,
.loading-backdrop .loading .loader {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation-fill-mode: both;
  animation: loading 1.8s infinite ease-in-out;
}
.loading-backdrop .loading .loader {
  margin: 1em auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  animation-delay: -0.16s;
}
.loading-backdrop .loading .loader:before {
  left: -3.5em;
  animation-delay: -0.32s;
}
.loading-backdrop .loading .loader:after {
  left: 3.5em;
}
.loading-backdrop .loading .loader:before,
.loading-backdrop .loader:after {
  content: '';
  position: absolute;
  top: 0;
}
.loading-backdrop .loading .text {
  text-align: center;
  font-size: 1.5rem;
  color: #2594e9;
  margin-top: 50px;
}
@keyframes loading {
0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em #2594e9;
}
40% {
    box-shadow: 0 2.5em 0 0 #2594e9;
}
}


