
.toast {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: #2594e9;
  color: #fff;
  height: 44px;
  line-height: 44px;
  text-align: center;
  font-size: 1.5rem;
}
.toast.toast-enter,
.toast.toast-leave-to {
  top: -50px;
  opacity: 0;
}
.toast.toast-enter-active,
.toast.toast-leave-active {
  transition: top 0.5s, opacity 0.2s;
}
.toast.center {
  top: 50%;
  left: 50%;
  height: auto;
  line-height: initial;
  border-radius: 4px;
  padding: 18px 12px;
  background-color: rgba(0,0,0,0.6);
  transform: translate(-50%, -50%);
}
.toast.center.toast-enter,
.toast.center.toast-leave-to {
  opacity: 0;
}
.toast.center.toast-enter-active,
.toast.center.toast-leave-active {
  transition: opacity 0.5s;
}


